<template>
  <div class="user">
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
      <!--  -->
      <el-form ref="form" :model="form" :inline="true" label-width="80px" :rules="rules">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model="form.age" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="form.sex" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="birth">
          <el-date-picker v-model="form.birth" type="date" placeholder="" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="addr">
          <el-input v-model="form.addr" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>

    <div class="header">
      <div>
        <el-button type="primary" @click="add"></el-button>
        <el-button type="primary" @click="exportExcel"></el-button>
      </div>
      <el-form :inline="true" :model="userForm">
        <el-form-item>
          <el-input v-model="userForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <el-table :data="tableData" height="90%" style="width: 100%" stripe @selection-change="selectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column prop="name" label="" width="120">
      </el-table-column>
      <el-table-column prop="sex" label="" width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.sex === 1 ? '' : '' }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="" width="120">
      </el-table-column>
      <el-table-column prop="birth" label="" width="150">
      </el-table-column>
      <el-table-column prop="addr" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" @click="edit(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination layout="total, sizes, prev, pager, next, jumper" :page-sizes="[5, 10, 20]" :page-size="pageData.limit"
      :total="total" @size-change="sizeChange" @current-change="currentPage">
    </el-pagination>
  </div>
</template>

<script>
import { getUser, addUser, editUser, delUser } from '../api'
import excel from '../utils/excel'
export default {
  name: 'UserView',
  data() {
    return {
      dialogVisible: false,
      form: {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: ''
      },
      rules: {
        name: [
          { required: true, message: '' },
        ],
        age: [
          { required: true, message: '' },
        ],
        sex: [
          { required: true, message: '' },
        ],
        birth: [
          { required: true, message: '' },
        ],
        addr: [
          { required: true, message: '' },
        ],
      },
      tableData: [],
      dialogType: 0,  // 01
      dialogTitle: '',
      total: 0,  // 
      pageData: {
        page: 1,
        limit: 5
      },
      userForm: {
        name: ''
      },
      multipleSelection: []
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    // 
    getList() {
      getUser({ ...this.userForm, ...this.pageData }).then(({ data }) => {
        // console.log(data)
        this.tableData = data.list
        this.total = data.count || 0
      })
    },
    add() {
      this.dialogType = 0
      this.dialogTitle = ''
      this.dialogVisible = true
    },
    submit() {
      // 
      this.$refs.form.validate((valid) => {
        // console.log(valid)
        if (valid) {
          // 
          // console.log('', this.form)
          if (this.dialogType === 0) {
            addUser(this.form).then(() => {
              // 
              this.$message.success('')
              this.getList()
            })
          } else {
            editUser(this.form).then(() => {
              this.getList()
            })
          }

        } else {
          this.$message.error('');
        }
      })
      this.handleClose()
    },
    cancel() {
      this.handleClose()
    },
    // 
    handleClose() {
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    edit(row) {
      // console.log(row)
      this.dialogType = 1
      this.dialogTitle = ''
      this.dialogVisible = true
      // 
      this.form = JSON.parse(JSON.stringify(row))
    },
    del(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delUser({ id: row.id }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.getList()
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    currentPage(val) {
      this.pageData.page = val
      this.getList()
    },
    // 
    sizeChange(val) {
      this.pageData.limit = val
      this.getList()
    },
    // 
    search() {
      this.getList()
    },
    selectionChange(val) {
      // console.log('multipleSelection', val)
      this.multipleSelection = val
    },
    exportExcel() {
      const params = {
        title: ['', '', '', '', ''],
        key: Object.keys(this.form),
        data: this.multipleSelection, // 
        autoWidth: true, //autoWidthtrue
        filename: ''
      }
      excel.exportArrayToExcel(params)
    }
  }
}
</script>

<style lang="less" scoped>
.user {
  height: 90%;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: start;
  }

  .el-pagination {
    margin-top: 15px;
    text-align: right;
    // background-color: #F5F5F5;
  }
}
</style>